.student-management {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.student-management h2, .student-management h3 {
  text-align: center;
  margin-bottom: 20px;
}

.student-management form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 400px;
  margin: 0 auto;
}

.student-management label {
  margin-bottom: 5px;
}

.student-management input[type="text"],
.student-management input[type="number"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.student-management button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.student-management button:hover {
  background-color: #0056b3;
}

.student-list ul {
  list-style-type: none;
  padding: 0;
}

.student-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.student-list button {
  padding: 5px 10px;
  border: none;
  background-color: #dc3545;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.student-list button:hover {
  background-color: #c82333;
}
.student-page {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.student-page h2, .student-page h3 {
  text-align: center;
  margin-bottom: 20px;
}

.student-page form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 400px;
  margin: 0 auto 20px auto;
}

.student-page label {
  margin-bottom: 5px;
}

.student-page input[type="text"],
.student-page input[type="number"],
.student-page input[type="date"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.student-page button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.student-page button:hover {
  background-color: #0056b3;
}

.payment-list ul {
  list-style-type: none;
  padding: 0;
}

.payment-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.payment-list button {
  padding: 5px 10px;
  border: none;
  background-color: #dc3545;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.payment-list button:hover {
  background-color: #c82333;
}

.chart-container {
  max-width: 300px;
  margin: 0 auto 20px auto;
}

.forms-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 400px;
  margin: 0 auto;
}